@import url("https://fonts.googleapis.com/css2?family=Arvo&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300&display=swap");

.container {
  height: 100%;
  background-color: rgb(255, 255, 255);
  position: relative;
}

.message {
  text-align: center;
  margin: 0;
  padding: 2rem;
}

.meal-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.meal-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0rem 1.2rem;
  height: 5rem;
  cursor: pointer;
}

.meal-container:hover {
  background-color: #f5f0e5;
}

.img {
  border-radius: 50%;
  width: 3.5rem;
  height: 3.5rem;
  margin-right: 1rem;
}

.title {
  margin: 0;
  color: #ed1b24;
  font-family: "Outfit", sans-serif;
  font-size: 0.9rem;
}

.description {
  margin: 0;
  margin-top: 0.4rem;
  font-family: "Arvo", serif;
  font-size: 0.75rem;
}

.buttons-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.2rem 1.5rem 1.2rem;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #f5f0e5;
  outline: 1px solid #d3d3d3;
  border: 0px;
  font-family: "Outfit", sans-serif;
  padding: 0.5rem;
  border-radius: 10px;
  width: 5rem;
  cursor: pointer;
  color: #ed1b24;
  font-weight: 900;
}

.button:hover {
  background-color: #d6d6d6;
}
