@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300&display=swap");

.ring {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
  background: transparent;
  border: 10px solid #ffb774;
  border-radius: 50%;
  text-align: center;
  line-height: 150px;
  font-family: "Outfit", sans-serif;
  font-size: 10px;
  color: #fa474a;
  letter-spacing: 4px;
  text-transform: uppercase;
  box-shadow: 0 0 20px rgba(8, 8, 8, 0.12);
  font-weight: bold;
}
.ring:before {
  content: "";
  position: absolute;
  top: -10px;
  left: -10px;
  width: 100%;
  height: 100%;
  border: 10px solid transparent;
  border-top: 10px solid #fa474a;
  border-right: 10px solid #fa474a;
  border-radius: 50%;
  animation: animateC 2s linear infinite;
}

@keyframes animateC {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes animate {
  0% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(405deg);
  }
}
