@import url("https://fonts.googleapis.com/css2?family=Arvo&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300&display=swap");

.bookmark-list {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: white;
  right: 0;
  top: 5.3rem;
  width: 20rem;
  max-height: 19.6rem;
  z-index: 2;
  overflow-y: scroll;
  flex-wrap: nowrap;
}

.meal-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.7rem 1.2rem;
  height: 5rem;
  cursor: pointer;
}

.meal-container:hover {
  background-color: #f5f0e5;
}

.img {
  border-radius: 50%;
  width: 3.5rem;
  height: 3.5rem;
  margin-right: 1rem;
}

.title {
  margin: 0;
  color: #ed1b24;
  font-family: "Outfit", sans-serif;
  font-size: 0.9rem;
}

.description {
  margin: 0;
  margin-top: 0.4rem;
  font-family: "Arvo", serif;
  font-size: 0.75rem;
}

.buttons-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.2rem 1.5rem 1.2rem;
}

.button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #f5f0e5;
  outline: 1px solid #d3d3d3;
  border: 0px;
  font-family: "Outfit", sans-serif;
  padding: 0.5rem;
  border-radius: 10px;
  width: 5rem;
  cursor: pointer;
  color: #ed1b24;
  font-weight: 900;
}

.button:hover {
  background-color: #d6d6d6;
}

.icon {
  color: #ff5e62;
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.default-text {
  font-family: "Outfit", sans-serif;
}

.default-text-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  text-align: center;
  padding: 3rem;
}

.bookmark-list::-webkit-scrollbar {
  width: 12px;
  height: 10px;
}

.bookmark-list::-webkit-scrollbar-thumb {
  background: #999999;
  border-radius: 5px;
}

.xmark {
  position: absolute;
  right: 0;
  color: #ed1b24;
  font-size: 1.3rem;
  margin-right: 1rem;
  transform: translateY(-50%);
}
