@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");

* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to right, #ffb066, #ff5e62);
  overflow-x: hidden;
}

#root {
  display: block;
  width: 0;
  height: 0;
}
